.Button {
    /* background-color: transparent; */
    border: none;
    color: rgb(207, 121, 121);
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 10px;
    margin: 20px;
    font-weight: bold;
}

.Button:disabled {
    color: #ccc;
    cursor: not-allowed;
}

.Success {
    color: #5C9210;
}

.Danger {
    color: #944317;
}